@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.recipe_recipe__gyQo1 {
	border-radius: 10px;
	box-shadow: 0px .5px 20px rgba(71, 71, 71, .8);

	margin: 20px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	background: whitesmoke;
	align-items: center;
	min-width: 40%;
}

.recipe_image__qWKcq {
	border-radius: 40%;
	width: 100px;
	height: 100px;
	margin-bottom: 1rem;
	box-shadow: 0px 2px 10px rgba(113, 231, 240, 7);
}

.App {
	min-height: 100vh;
	background-color: #ff7979;
	font-family: 'Poppins', sans-serif;
}
.search-form {
	min-height: 10vh;
	display: flex;
	justify-content: center;
	align-items: center;
}
.search-bar {
	width: 50%;
	border: none;
	margin-top: 4em;
	margin-bottom: 3em;
	padding: 20px;
}

.search-button {
	background: #f7d794;
	border: none;
	margin-top: 4em;
	margin-bottom: 3em;
	padding: 20px;

	color: #303952;
}

.recipes {
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
}

.ingredients-title {
	font-size: 27px;
	text-align: start;
	color: #3c6382;
}

.t-title {
	font-size: 36px;
	color: #303952;
}

.calo1 {
	color: #0a9e81;
}

